ol, ul {
	list-style: none;
    margin: 0;
	padding: 0;
}

html {
    font-size: 100%;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

a:focus {
    outline: none;
}

a:active,
a:hover {
    outline: 0;
}

img {
    border: 0;
    -ms-interpolation-mode: bicubic;
}

figure {
    margin: 0;
}

button,
input,
select,
textarea {
    font-size: 100%;
    margin: 0;
    vertical-align: baseline;
    *vertical-align: middle;
}

button,
input {
    line-height: normal;
}

button,
select {
    text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
    *overflow: visible;
}

button[disabled],
html input[disabled] {
    cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

img {
    vertical-align: middle;
}

body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  background-color: #16192A;
  color: white;
}

.wrapper {
  padding: 25px 0;
  text-align: center;
}

header {
  font-size: 18px;
  text-align: left;
  margin-bottom: 10px;
}

h1 {
    font-size: 30px;
    margin: 20px auto 0 auto;
}

.about__link {
    margin: 20px auto;
    font-weight: 400;
    font-size: 18px;
}

.link_gif {
    font-size: 16px;
}

form {
    margin-bottom: 40px;
}

.footer {
    position: fixed;
    bottom: 0;
    left: calc(50% - 51.7px);
}

a {
  text-decoration: none;
  color: white;
  transition: all 0.15s linear;
}

a:hover {
  color: #FF9B5E;
}

a:focus, a:hover, a:visited, a:link, a:active {
  text-decoration: none;
}

img {
  max-height: 200px;
}

.btn {
  min-width: 150px;
}

.btn-primary {
    background: linear-gradient(-45deg, #9830FF, #FC3355, #9830FF, #2740FF);
	background-size: 300% 300%;
    border: transparent;
    -webkit-animation: 1s ease;
            animation: 1s ease;
}

.btn-primary:not(.btn-primary--send){
    margin: 50px;
}

.btn-primary:hover {
    -webkit-animation: animate_gradient 1.5s ease forwards;
            animation: animate_gradient 1.5s ease forwards;
}

.form-control, .form-control:focus {
    background-color: #1F243A;
    color: white;
    border: none;
    resize: none;
}

.form-control::-webkit-scrollbar {
    width: 10px;
}
   
.form-control::-webkit-scrollbar-track {
    background-color: transparent;
}
   
.form-control::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(0deg, #9830FF, #2740FF);
}

.form-control:focus {
    box-shadow: 0 0 13px 2px #2740FF;
}

@-webkit-keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

@keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}
.todo__notodo, .todo__item {
    position: relative;
    width: 400px;
    margin: 10px auto;
    border-radius: 10px;
}

.todo__notodo {
    background: linear-gradient(to top left, #1D40FF, #1DF6FD);
    padding: 29px 0;
    margin-top: 30px;
}
.todo__item {
    padding: 5px 0;
    text-align: left;
    background: linear-gradient(to top left, #1D40FF, #1DF6FD);
}

.todo__item:first-child {
    margin-top: 30px;
}

.todo__item:nth-child(2n) {
    background: linear-gradient(to top left, #FC3355, #CE5BF6);
}

.todo__item:nth-child(3n) {
    background: linear-gradient(to top left, #9830FF, #2740FF);
}

.todo__item:last-child{
    margin-bottom: 30px;
}

.todo__item .done {
    text-decoration: line-through;
}

.todo__item p {
    max-width: 400px;
    line-height: 1.5;
    padding: 24px 5px 24px 50px;
    margin: 0;
}

.todo__text {
    background-color: #1F243A;
    border: none;
    border-radius: 10px;
    width: 400px;
    height: 100px;
    margin: auto;
    color: white;
    padding: 10px;
    resize: none;
    margin-bottom: 10px;
}

.todo__text:focus{
    box-shadow: 0 0 13px 2px #2740FF;
}

.todo__text::-webkit-scrollbar {
    width: 10px;
}
   
.todo__text::-webkit-scrollbar-track {
    background-color: transparent;
}
   
.todo__text::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(0deg, #9830FF, #2740FF);
}

.todo__button_create {
    color: white;
    font-size: 18px;
    padding: 15px 132px;
    border: none;
    border-radius: 10px;
    margin: auto;
    background: linear-gradient(-45deg, #9830FF, #FC3355, #9830FF, #2740FF);
	background-size: 300% 300%;
}

@-webkit-keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

@keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

.todo__button_create:hover {
    -webkit-animation: animate_gradient 1s ease forwards;
            animation: animate_gradient 1s ease forwards;
}

.todo__button_remove {
    background-color: transparent;
    position: absolute;
    top: calc(50% - 12.5px);
    right: -35px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: none;
    color: white;
    font-size: 30px;
    line-height: 20px;
    transition: all 0.15s linear;
}

.todo__button_remove:hover {
    color: #FF9B5E;
}

.form-check-input {
    position: absolute;
    border-radius: 50%;
    top: calc(50% - 8px);
    left: 20px;
    transition: all 0.15s linear;
    cursor: pointer;
}

.form-check-input:hover {
    background-color: transparent;
    border-color: white;
}

.form-check-input:checked {
    background-size: 25px;
    background-color: transparent;
    border-color: transparent;
}
.form-check-input:focus {
    box-shadow: 0 0 0 .25rem transparent;
}

@media only screen and (max-width : 800px)  {
    .todo__text, .todo__notodo, .todo__item {
        width: 300px;
    }

    .todo__button_create {
        padding: 15px 82px;
    }
}


@media only screen and (max-width : 600px)  {
    .todo__text, .todo__notodo, .todo__item {
        width: 275px;
    }

    .todo__button_create {
        padding: 15px 70px;
    }

    .todo__item p {
        padding: 24px 35px 24px 50px;
    }

    .todo__button_remove {
        right: 10px;
    }
}

@media only screen and (max-width : 360px)  {
    .todo__text, .todo__notodo, .todo__item {
        width: 225px;
    }

    .todo__button_create {
        padding: 15px 47px;
    }
}
.calculator {
  border-radius: 12px;
  margin: auto;
  max-width: 300px;
  overflow: hidden;
  box-shadow: 0 0 20px 0 #1D40FF;
}

.calculator__display {
  background: #1F243A;
  color: white;
  font-size: 25px;
  width: inherit;
  padding: 1em 0;
}

.calculator__display input {
  width: 250px;
  background-color: inherit;
  color: inherit;
  outline: none;
  border: none;
  text-align: right;
}

.calculator__buttons {
  display: -ms-grid;
  display: grid;
  background-color: #1F243A;
  grid-gap: 1px;
  -ms-grid-columns: 1fr 1px 1fr 1px 1fr 1px 1fr;
  grid-template-columns: repeat(4, 1fr);
}

.calculator button {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  color: white;
  font-size: 25px;
  cursor: pointer;
  padding: 22px 0;
  background: linear-gradient(-45deg, #9830FF, #FC3355, #9830FF, #2740FF);
  background-size: 400% 400%;
}

@-webkit-keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

@keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

.calculator button:nth-child(12) {
  grid-column: -2;
  -ms-grid-row: 2;
  -ms-grid-row-span: 3;
  grid-row: 2 / span 3;
}

.calculator button:nth-child(15) {
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-column: 1 / span 2;
}

.calculator button:hover {
  -webkit-animation: animate_gradient 0.5s ease forwards;
          animation: animate_gradient 0.5s ease forwards;
}

.calculator button:focus {
  outline: none;
  background-color: orange;
}
