.todo__notodo, .todo__item {
    position: relative;
    width: 400px;
    margin: 10px auto;
    border-radius: 10px;
}

.todo__notodo {
    background: -webkit-gradient(linear, right bottom, left top, from(#1D40FF), to(#1DF6FD));
    background: -o-linear-gradient(bottom right, #1D40FF, #1DF6FD);
    background: linear-gradient(to top left, #1D40FF, #1DF6FD);
    padding: 29px 0;
    margin-top: 30px;
}
.todo__item {
    padding: 5px 0;
    text-align: left;
    background: -webkit-gradient(linear, right bottom, left top, from(#1D40FF), to(#1DF6FD));
    background: -o-linear-gradient(bottom right, #1D40FF, #1DF6FD);
    background: linear-gradient(to top left, #1D40FF, #1DF6FD);
}

.todo__item:first-child {
    margin-top: 30px;
}

.todo__item:nth-child(2n) {
    background: -webkit-gradient(linear, right bottom, left top, from(#FC3355), to(#CE5BF6));
    background: -o-linear-gradient(bottom right, #FC3355, #CE5BF6);
    background: linear-gradient(to top left, #FC3355, #CE5BF6);
}

.todo__item:nth-child(3n) {
    background: -webkit-gradient(linear, right bottom, left top, from(#9830FF), to(#2740FF));
    background: -o-linear-gradient(bottom right, #9830FF, #2740FF);
    background: linear-gradient(to top left, #9830FF, #2740FF);
}

.todo__item:last-child{
    margin-bottom: 30px;
}

.todo__item .done {
    text-decoration: line-through;
}

.todo__item p {
    max-width: 400px;
    line-height: 1.5;
    padding: 24px 5px 24px 50px;
    margin: 0;
}

.todo__text {
    background-color: #1F243A;
    border: none;
    border-radius: 10px;
    width: 400px;
    height: 100px;
    margin: auto;
    color: white;
    padding: 10px;
    resize: none;
    margin-bottom: 10px;
}

.todo__text:focus{
    -webkit-box-shadow: 0 0 13px 2px #2740FF;
            box-shadow: 0 0 13px 2px #2740FF;
}

.todo__text::-webkit-scrollbar {
    width: 10px;
}
   
.todo__text::-webkit-scrollbar-track {
    background-color: transparent;
}
   
.todo__text::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: -webkit-gradient(linear, left bottom, left top, from(#9830FF), to(#2740FF));
    background: linear-gradient(0deg, #9830FF, #2740FF);
}

.todo__button_create {
    color: white;
    font-size: 18px;
    padding: 15px 132px;
    border: none;
    border-radius: 10px;
    margin: auto;
    background: -o-linear-gradient(135deg, #9830FF, #FC3355, #9830FF, #2740FF);
    background: linear-gradient(-45deg, #9830FF, #FC3355, #9830FF, #2740FF);
	background-size: 300% 300%;
}

@-webkit-keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

@keyframes animate_gradient {
	from { background-position: 0% 25%; }
	to { background-position: 50% 25%; }
}

.todo__button_create:hover {
    -webkit-animation: animate_gradient 1s ease forwards;
            animation: animate_gradient 1s ease forwards;
}

.todo__button_remove {
    background-color: transparent;
    position: absolute;
    top: calc(50% - 12.5px);
    right: -35px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: none;
    color: white;
    font-size: 30px;
    line-height: 20px;
    -webkit-transition: all 0.15s linear;
    -o-transition: all 0.15s linear;
    transition: all 0.15s linear;
}

.todo__button_remove:hover {
    color: #FF9B5E;
}

.form-check-input {
    position: absolute;
    border-radius: 50%;
    top: calc(50% - 8px);
    left: 20px;
    -webkit-transition: all 0.15s linear;
    -o-transition: all 0.15s linear;
    transition: all 0.15s linear;
    cursor: pointer;
}

.form-check-input:hover {
    background-color: transparent;
    border-color: white;
}

.form-check-input:checked {
    background-size: 25px;
    background-color: transparent;
    border-color: transparent;
}
.form-check-input:focus {
    -webkit-box-shadow: 0 0 0 .25rem transparent;
            box-shadow: 0 0 0 .25rem transparent;
}

@media only screen and (max-width : 800px)  {
    .todo__text, .todo__notodo, .todo__item {
        width: 300px;
    }

    .todo__button_create {
        padding: 15px 82px;
    }
}


@media only screen and (max-width : 600px)  {
    .todo__text, .todo__notodo, .todo__item {
        width: 275px;
    }

    .todo__button_create {
        padding: 15px 70px;
    }

    .todo__item p {
        padding: 24px 35px 24px 50px;
    }

    .todo__button_remove {
        right: 10px;
    }
}

@media only screen and (max-width : 360px)  {
    .todo__text, .todo__notodo, .todo__item {
        width: 225px;
    }

    .todo__button_create {
        padding: 15px 47px;
    }
}